.footerBlock {
    background-color: #0ea0ac;
}

.container {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: #00b0c8;
}

.socialContainer {
    border: 1px solid;
    background-color: #7f88ff;
    width: 250px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    align-items: center;
}

.social {
    width: 50px;
    height: 50px;
    background-color: #ac6a1b;
}